// src/components/WordCardDisplay.jsx
import React, { useState, useEffect, useMemo } from 'react';
import { getFirestore, doc, onSnapshot, updateDoc, arrayUnion, arrayRemove, serverTimestamp, setDoc } from 'firebase/firestore';
import { Loader2, BookOpen, XCircle, RefreshCw } from 'lucide-react'; // RefreshCw ikonu eklendi
import WordCard from './WordCard';

// Firebase yaplandrma ve balatma (prop olarak geldii iin dorudan kullanlmayacak)
// import { auth, db, firebaseConfig } from '../config/firebase';

const API_BASE_URL = 'https://generativelanguage.googleapis.com/v1beta/models/gemini-2.0-flash:generateContent';
const API_KEY = "AIzaSyCSuzlRr7AmF59CsaNC9S5Asa-U9Rpx7Mo"; // Canvas ortam bu anahtar otomatik olarak salayacaktr.

const WordCardDisplay = ({ userId, db, firebaseAppId, saveProgress }) => {
  const [wordsToLearn, setWordsToLearn] = useState([]);
  const [currentWordIndex, setCurrentWordIndex] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  const [statusMessage, setStatusMessage] = useState('');
  const [isGeneratingWords, setIsGeneratingWords] = useState(false); // Yeni kelime retme durumu

  // Firestore belge referans - useMemo ile optimize et
  const userProgressDocRef = useMemo(() => {
    return userId && db ? doc(db, `artifacts/${firebaseAppId}/users/${userId}/progress/userProgress`) : null;
  }, [userId, db, firebaseAppId]);

  // Yapay zekadan kelime ekme fonksiyonu
  const fetchWordsFromAI = async () => {
    setIsGeneratingWords(true);
    setStatusMessage("YDS'ye ynelik yeni kelimeler retiliyor...");
    console.log("AI'dan kelime ekme balatld.");

    const prompt = `YDS snavna hazrlananlar iin 5 adet ileri seviye ngilizce kelime, Trke anlamlar ve ngilizce rnek cmleleri ile birlikte JSON formatnda listele. JSON yaps u ekilde olmal: [{"word": "kelime", "meaning": "anlam", "example": "rnek cmle"}]. rnek cmleler kelimenin akademik kullanmn yanstmal.`;

    try {
      const payload = {
        contents: [{ role: 'user', parts: [{ text: prompt }] }],
        generationConfig: {
          responseMimeType: "application/json",
          responseSchema: {
            type: "ARRAY",
            items: {
              type: "OBJECT",
              properties: {
                "word": { "type": "STRING" },
                "meaning": { "type": "STRING" },
                "example": { "type": "STRING" }
              },
              "propertyOrdering": ["word", "meaning", "example"]
            }
          }
        }
      };

      const response = await fetch(`${API_BASE_URL}?key=${API_KEY}`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(`API hatas: ${response.status} - ${errorData.error?.message || 'Bilinmeyen hata'}`);
      }

      const result = await response.json();
      console.log("AI'dan gelen ham yant:", result);

      if (result.candidates && result.candidates.length > 0 &&
          result.candidates[0].content && result.candidates[0].content.parts &&
          result.candidates[0].content.parts.length > 0) {
        const jsonString = result.candidates[0].content.parts[0].text;
        const generated = JSON.parse(jsonString);

        if (Array.isArray(generated) && generated.length > 0) {
          setWordsToLearn(generated);
          setCurrentWordIndex(0);
          setStatusMessage("Yeni kelimeler baaryla retildi!");
          console.log("AI tarafndan retilen kelimeler:", generated);
          return generated; // retilen kelimeleri dndr
        } else {
          throw new Error("Yapay zekadan geerli kelime listesi alnamad.");
        }
      } else {
        throw new Error("Yapay zekadan beklenen yant format alnamad.");
      }
    } catch (error) {
      console.error("Yapay zekadan kelime ekilirken hata:", error);
      setStatusMessage(`Kelime retme hatas: ${error.message}. Ltfen tekrar deneyin.`);
      setWordsToLearn([]); // Hata durumunda kelime listesini temizle
      return [];
    } finally {
      setIsGeneratingWords(false);
      setIsLoading(false); // lk ykleme bitti
    }
  };

  // Kelimeleri Firestore'dan ek veya yapay zekadan ret
  useEffect(() => {
    console.log("WordCardDisplay useEffect triggered for data fetching.");
    
    if (!userProgressDocRef) {
      console.log("userProgressDocRef is null. Setting isLoading to false and skipping fetch.");
      setIsLoading(false);
      return;
    }

    console.log("Setting up onSnapshot listener for user progress.");
    
    const unsubscribe = onSnapshot(
      userProgressDocRef, 
      async (docSnap) => { // async eklendi
        console.log("onSnapshot callback triggered.");
        let learnedWords = [];
        if (docSnap.exists()) {
          const data = docSnap.data();
          learnedWords = data.learnedWords || [];
          console.log("Firestore data received. Learned words:", learnedWords);
        } else {
          console.log("User progress document not found. Creating initial document.");
          const initialData = {
            reading: { correct: 0, total: 0 },
            learnedWords: [],
            activities: []
          };
          await setDoc(userProgressDocRef, initialData); // Belgeyi olutur
          console.log("Initial progress document created.");
        }

        // renilecek kelime kalmadysa veya henz hi kelime yoksa yeni kelimeler ret
        // Sadece ilk yklemede veya wordsToLearn boken AI'dan kelime ek
        if (wordsToLearn.length === 0 && !isGeneratingWords) {
            console.log("No words to learn or initial load. Fetching new words from AI.");
            const generated = await fetchWordsFromAI();
            // Eer AI'dan kelime geldiyse ve bunlar daha nce renilmemise ekle
            if (generated && generated.length > 0) {
                const availableWords = generated.filter(
                    (wordObj) => !learnedWords.includes(wordObj.word)
                );
                setWordsToLearn(availableWords);
                setCurrentWordIndex(0);
                if (availableWords.length === 0) {
                    setStatusMessage("retilen tm kelimeler zaten renilmi. Yeni kelime retmeyi deneyin.");
                }
            } else if (learnedWords.length > 0) {
                // Eer hi yeni kelime retilemediyse ve renilmi kelimeler varsa,
                // kullancya tekrar balama seenei sunmak iin bo brak
                setWordsToLearn([]);
            }
        } else if (wordsToLearn.length > 0) {
            // Firestore'dan gelen renilen kelimeleri mevcut listeden filtrele
            const updatedAvailableWords = wordsToLearn.filter(
                (wordObj) => !learnedWords.includes(wordObj.word)
            );
            setWordsToLearn(updatedAvailableWords);
            // Eer mevcut kelime index'i artk geerli deilse sfrla
            if (currentWordIndex >= updatedAvailableWords.length) {
                setCurrentWordIndex(0);
            }
        }
        setIsLoading(false);
        console.log("isLoading set to false after onSnapshot callback.");
      }, 
      (error) => {
        console.error("Error loading words or setting up snapshot:", error);
        setStatusMessage("Kelimeler yklenirken hata olutu.");
        setIsLoading(false);
        setWordsToLearn([]); // Hata durumunda kelime listesini temizle
        console.log("isLoading set to false after onSnapshot error.");
      }
    );

    return () => {
      console.log("Cleaning up onSnapshot listener.");
      unsubscribe();
    };
  }, [userProgressDocRef, isGeneratingWords, wordsToLearn.length]); // wordsToLearn.length eklendi

  const handleNextWord = () => {
    if (currentWordIndex < wordsToLearn.length - 1) {
      setCurrentWordIndex(currentWordIndex + 1);
    } else {
      // Tm kelimeler gzden geirildiinde yeni kelimeler retmeyi dene
      setStatusMessage("Tm kelimeleri gzden geirdiniz. Yeni kelimeler retiliyor...");
      fetchWordsFromAI(); // Yeni kelimeler ret
    }
  };

  const handleMarkAsLearned = async (word) => {
    if (!userProgressDocRef) {
      setStatusMessage("Kullanc oturumu ak deil veya veritaban hazr deil.");
      return;
    }
    
    setStatusMessage(`"${word}" renildi olarak iaretleniyor...`);
    
    try {
      if (saveProgress) {
        await saveProgress({
          learnedWords: arrayUnion(word),
          activities: arrayUnion({
            text: `"${word}" kelimesini rendiniz.`,
            timestamp: new Date()
          })
        });
      }
      
      setStatusMessage(`"${word}" baaryla renildi olarak iaretlendi!`);
      
      // Kelimeyi listeden kar ve bir sonraki kelimeye ge
      setWordsToLearn(prevWords => prevWords.filter(w => w.word !== word));
      setCurrentWordIndex(prevIndex => {
        const newIndex = prevIndex;
        // Eer mevcut index, yeni filtrelenmi listeden bykse veya liste bosa 0'a dn
        if (newIndex >= wordsToLearn.filter(w => w.word !== word).length && wordsToLearn.filter(w => w.word !== word).length > 0) {
          return 0;
        } else if (wordsToLearn.filter(w => w.word !== word).length === 0) {
          return 0; // Liste bosa 0'a dn
        }
        return newIndex;
      });

      // Eer renilecek kelime kalmadysa yeni kelimeler ret
      // Bu kontrol, setWordsToLearn tamamlandktan sonra yapmalyz
      // veya wordsToLearn.length'i filtreledikten sonraki haliyle kontrol etmeliyiz.
      // setTimeout kullanarak state gncellemesinin bitmesini bekleyelim.
      setTimeout(() => {
        if (wordsToLearn.filter(w => w.word !== word).length === 0) {
          setStatusMessage("Tm kelimeleri rendiniz. Yeni kelimeler retiliyor...");
          fetchWordsFromAI();
        } else {
          setStatusMessage(''); // Baar mesajn temizle
        }
      }, 500); // Ksa bir gecikme
      
    } catch (error) {
      console.error("Error marking word as learned:", error);
      setStatusMessage('Kaydetme hatas: ' + error.message);
    }
  };

  // Status mesajn otomatik temizle
  useEffect(() => {
    if (statusMessage) {
      const timer = setTimeout(() => {
        setStatusMessage('');
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [statusMessage]);

  // "Tekrar Bala" butonu iin: renilen kelimeleri sfrla ve yeni kelimeler ret
  const handleResetWords = async () => {
    if (userProgressDocRef && saveProgress) {
      setStatusMessage("Kelime listesi sfrlanyor ve yeni kelimeler retiliyor...");
      try {
        await saveProgress({ learnedWords: [] }); // renilen kelimeleri Firestore'dan temizle
        await fetchWordsFromAI(); // Yeni kelimeler ret
        setStatusMessage("Kelime listesi sfrland ve yeni kelimeler retildi!");
      } catch (e) {
        console.error("Error resetting words or fetching new ones:", e);
        setStatusMessage("Sfrlama veya kelime retme hatas: " + e.message);
      }
    }
  };


  if (isLoading || isGeneratingWords) {
    return (
      <div className="flex justify-center items-center h-full min-h-[400px]">
        <Loader2 className="animate-spin text-violet-500" size={32} />
        <p className="ml-2 text-lg">{isGeneratingWords ? "Yapay zeka kelimeler retiyor..." : "Kelimeler Ykleniyor..."}</p>
      </div>
    );
  }

  return (
    <div className="p-8 animate-fade-in flex flex-col items-center justify-center min-h-[calc(100vh-64px)]">
      <h2 className="text-3xl font-bold text-slate-800 dark:text-white mb-8 flex items-center">
        <BookOpen className="mr-3 text-violet-600" size={32} />
        Kelime Kartlar
      </h2>

      {wordsToLearn.length > 0 && wordsToLearn[currentWordIndex] ? (
        <>
          <div className="mb-4 text-center">
            <p className="text-slate-600 dark:text-slate-400">
              {currentWordIndex + 1} / {wordsToLearn.length} kelime
            </p>
          </div>
          
          <WordCard
            word={wordsToLearn[currentWordIndex].word}
            meaning={wordsToLearn[currentWordIndex].meaning}
            example={wordsToLearn[currentWordIndex].example}
            onMarkAsLearned={handleMarkAsLearned}
          />
          
          <div className="mt-8 flex space-x-4">
            <button
              onClick={handleNextWord}
              className="py-3 px-6 bg-purple-600 hover:bg-purple-700 text-white font-semibold rounded-full shadow-md transition duration-300 ease-in-out transform hover:scale-105 flex items-center justify-center"
            >
              <XCircle size={20} className="mr-2" />
              Sonraki Kelime
            </button>
            <button
              onClick={fetchWordsFromAI} // Yeni kelime retme butonu
              disabled={isGeneratingWords}
              className="py-3 px-6 bg-sky-600 hover:bg-sky-700 disabled:bg-sky-400 text-white font-semibold rounded-full shadow-md transition duration-300 ease-in-out transform hover:scale-105 flex items-center justify-center"
            >
              {isGeneratingWords ? (
                <Loader2 className="animate-spin mr-2" size={20} />
              ) : (
                <RefreshCw size={20} className="mr-2" />
              )}
              {isGeneratingWords ? 'retiliyor...' : 'Yeni Kelimeler ret'}
            </button>
          </div>
        </>
      ) : (
        <div className="bg-white dark:bg-slate-800 p-6 rounded-xl shadow-lg border border-violet-100 dark:border-slate-700 text-center">
          <p className="text-slate-500 dark:text-slate-400 text-lg mb-4">
            {isLoading ? "Kelimeler Ykleniyor..." : "renilecek kelime yok veya yeni kelimeler retiliyor."}
          </p>
          {!isLoading && wordsToLearn.length === 0 && (
            <>
              <p className="text-slate-600 dark:text-slate-300 text-sm mb-4">
                Yeni kelimeler retmek veya renilen kelimeleri sfrlayp tekrar balamak iin aadaki butona tklayabilirsiniz.
              </p>
              <button
                onClick={handleResetWords} // "Tekrar Bala" butonu
                disabled={isGeneratingWords}
                className="py-2 px-4 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-full transition duration-300 transform hover:scale-105 flex items-center justify-center"
              >
                {isGeneratingWords ? (
                  <Loader2 className="animate-spin mr-2" size={20} />
                ) : (
                  <RefreshCw size={20} className="mr-2" />
                )}
                {isGeneratingWords ? 'retiliyor...' : 'Tekrar Bala / Yeni ret'}
              </button>
            </>
          )}
        </div>
      )}

      {statusMessage && (
        <div className="mt-4 p-3 text-sm rounded-lg bg-blue-100 text-blue-700 dark:bg-blue-800 dark:text-blue-200 text-center max-w-md">
          {statusMessage}
        </div>
      )}
    </div>
  );
};

export default WordCardDisplay;
